.button {
  border-radius: 3px;
  padding: 5px 24px;
  min-width: 132px;
  min-height: 30px;
  border: none;
  font-weight: 600;
  font-size: 14px;
  line-height: 143%;
  color: #fff;
  cursor: pointer;
}

.disabled {
  background: #004d99;
}

.secondary {
  border: 1px solid #06c;
  border-radius: 3px;
  padding: 5px 24px;
  width: 176px;
  height: 30px;
  color: #06c;
}

.default {
  background-color: #06c;
  color: white;
}

.red {
  background-color: #cc1439;
}

.default:hover {
  background-color: #0080ff;
}

.red:hover {
  background-color: #ff1a47;
}

.secondary:hover {
  background-color: #e5f0fa;
}

.default:active {
  background-color: #0059b2;
}

.secondary:active {
  background-color: #c9e5ff;
}

.red:active {
  background-color: #b21232;
}

.button:disabled {
  background-color: #004d99;
  opacity: 0.5;
  color: #fff;
}
